// 配置路由的地方
import Vue from "vue";
import VueRouter from "vue-router";

// 使用插件
Vue.use(VueRouter)

// 引入路由组件
import Convert from '@/pages/Convert/Convert'
import Bin2Dec from "@/pages/Bin2Dec/Bin2Dec"
import Home from "@/pages/Home/Home"
import DynamicCss from "@/pages/DynamicCss/DynamicCss"

// 配置路由，需要先暴露一个vue-router类的实例
export default new VueRouter({
    // 配置路由
    routes: [
        // 主页
        {
            path: "/home",
            component: Home,
            meta: { show: true }
        },
        {
            // html-js互相转换
            path: "/convert",
            component: Convert,
            meta: { show: true }
        },
        {
            // 二进制
            path: "/bin2dec",
            component: Bin2Dec,
            meta: { show: false }
        },
        // 动态调整样式
        {
            path: "/dynamicCss",
            component: DynamicCss,
            meta: { show: false }
        },
        // {
        //     path: "/search/:keyword",
        //     component: Search,
        //     meta: { show: true },
        //     name: 'search'
        // },
        {
            // 重定向，在项目跑起来的时候，访问/，立马重定向到主页
            path: "*",
            redirect: 'Home'
        },
    ]
})
